:root {
	--wp-fundi-hover-color: #283784;
	--wp-fundi-carousel-max-height: 400px;
	--wp-fundi-carousel-padding: 30px 30px 30px 30px;
	--wp-fundi-carousel-gap: 30px;
	--wp-fundi-swiper-background-color: var(--global-palette9, #ffffff);
	--wp-fundi-jobs-carousel-background-color: var(--global-palette9, #ffffff);
	--wp-fundi-jobs-text-color: var(--global-palette4, #2D3748);
	--wp-fundi-jobs-overlay-text-color: var(--global-palette7, #EDF2F7);
	--wp-fundi-jobs-overlay-background-color: var(--global-palette2, #215387);
	--wp-fundi-jobs-carousel-border-color: var(--global-palette6, #718096);
}

.wp-fundi-carousel-contents.swiper {
	width: 100%;
	height: calc(100% + 10px);
	background-color: var(--wp-fundi-jobs-carousel-background-color, #ffffff);
	color: var(--wp-fundi-jobs-text-color, #2D3748);
	justify-content: center;

	.swiper-wrapper {
		justify-content: center;

		.swiper-slide {
			text-align: center;
			font-size: 18px;
			background: var(--wp-fundi-swiper-background-color, #ffffff);
			display: flex;
			justify-content: center;
			align-items: center;
			overflow: visible;

			&.wp-fundi-carousel-content {
				display: flex;
				flex-direction: column;
				justify-content: flex-start;
				gap: var(--wp-fundi-carousel-gap, 30px);

				.wp-fundi-title {
					font-weight: 700;
					font-size: 18px;
				}

				.wp-fundi-company,
				.wp-fundi-blurb {
					font-weight: 400;
					font-size: 16px;
					margin: 20px;
				}
			}
		}
	}
}

.is-style-wp-fundi-rounded {
	.wp-fundi-carousel-content {

		.wp-fundi-featured-image,
		.wp-fundi-image-overlay {
			border-radius: 50%;
			aspect-ratio: 1 / 1;
			display: block;
			width: 100%;
			object-fit: cover;
			background-color: var(--wp-fundi-jobs-overlay-background-color, #215387);
			color: var(--wp-fundi-jobs-overlay-text-color, #EDF2F7);
			height: auto;
			display: flex;
			flex-direction: column;
			align-items: center;
			align-content: center;
			justify-content: center;
			overflow: visible;
			box-sizing: border-box;
		}

		.wp-fundi-featured-image .wp-fundi-image img {
			border-radius: 50%;
			aspect-ratio: 1 / 1;
			display: block;
			width: 100%;
			height: auto;
			object-fit: cover;
		}

	}
}

.is-style-default {
