			display: block;
			width: 100%;
			// height: 100%;
			object-fit: cover;
			background-color: var(--wp-fundi-jobs-overlay-background-color, #215387);
			color: var(--wp-fundi-jobs-overlay-text-color, #EDF2F7);
			height: auto;
			display: flex;
			flex-direction: column;
			align-items: center;
			align-content: center;
			justify-content: center;
