			box-sizing: border-box;
		}

		.wp-fundi-featured-image .wp-fundi-image img {
			border-radius: 0%;
			aspect-ratio: 1 / 1;
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}

